
.github-section {
    text-align: center;
  }
  
  .grid_about-section {
    text-align: center;
    margin: 50px auto;
    width: 60%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
  }
  .github-section-1 {
    padding: 15px;
    width: 80%;
    margin: 20px auto;
   
  }
  .react-activity-calendar {
    margin: auto;
  }
  
  @media screen and (max-width: 1050px) {
    .github-section {
      width: 100%;
    }
    .grid_about-section {
      width: 85%;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 20px;
    }
    .grid_about-section img {
      width: 90%;
    }
    .github-section-1 {
      width: 90%;
      margin: 10px auto;
    }
  }
  @media screen and (max-width: 950px) {
    .github-section {
      width: 100%;
    }
    .grid_about-section {
      width: 85%;
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 20px;
    }
    .grid_about-section img {
      width: 85%;
    }
    .github-section-1 {
      width: 90%;
      margin: 10px auto;
    }
  }
  @media screen and (max-width: 382px) {
    .grid_about-section {
      width: 90%;
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 20px;
    }
    .github-section-1 {
      width: 90%;
      margin: 10px auto;
    }
    .react-activity-calendar {
      margin: auto;
      width: 100%;
      font-size: 15px;
    }
  }